import React, { useState, useEffect, useContext } from "react";

import axios from "axios";

import { FaWhatsappSquare } from "react-icons/fa";
import { FaFacebook } from "react-icons/fa";
import { FaLinkedin } from "react-icons/fa";

import {
  FacebookShareButton,
  LinkedinShareButton,
  WhatsappShareButton,
} from "react-share";

import Navbar from "../../genral_components/Navbar/Navbar";

import Footer from "../../genral_components/Footer/Footer";

import "./BlogNewPage.css";

import { useLocation } from "react-router-dom";

import { Helmet } from "react-helmet";

import { InfinitySpin } from "react-loader-spinner";

export default function BlogNewPage() {
  const location = useLocation();

  const queryParams = new URLSearchParams(location.search);

  // const id = queryParams.get("id");

  const title = queryParams.get("title");

  const [posts, setPosts] = useState(null);

  useEffect(() => {
    window.scrollTo(0, 0);

    // let url = `https://www.interac8digital.com/wordpress/wp-json/wp/v2/posts/${id}?_embed`;

    const slug = convertToSlug(title);

    console.log("This is title", slug);

    let url = `https://www.interac8digital.com/wordpress/wp-json/wp/v2/posts?slug=${slug}&_embed`;

    axios.get(url).then((response) => {
      setPosts(response.data[0]);
    });
  }, [location]);

  function convertToSlug(inputString) {
    return inputString
      .toLowerCase() // Convert to lowercase
      .replace(/[^a-z0-9\s-]/g, "") // Remove non-alphanumeric characters except spaces and hyphens
      .replace(/\s+/g, "-") // Replace spaces with hyphens
      .replace(/-+/g, "-") // Replace multiple consecutive hyphens with a single hyphen
      .trim(); // Trim any leading or trailing spaces or hyphens
  }

  return (
    <>
      <div className="blog-new-page-main-container">
        <Navbar />
        <div className="blog-new-page-main-container-secondary">
          <div className="blog-new-page-main-container-secondary-main-image">
            {posts && posts._embedded["wp:featuredmedia"] ? (
              <>
                <img
                  src={
                    posts._embedded["wp:featuredmedia"][0].media_details.sizes
                      .full.source_url
                  }
                  className="banner-image"
                  alt="post banner"
                />

                {/*  */}

                <Helmet>
                  <title>Blogs New Page</title>

                  <meta
                    property="og:title"
                    // content={
                    //   (posts && posts._embedded["wp:featuredmedia"])
                    //     ? posts.title.rendered
                    //     : "fetching title"
                    // }
                    content={
                      posts && posts.title.rendered
                        ? posts.title.rendered
                        : "Loading..."
                    }
                  />

                  <meta
                    property="og:type"
                    content="digital marketing agency blog"
                  />

                  <meta
                    property="og:url"
                    // content= {`https://www.interac8digital.com/blogNewPage?id=${id}`}
                    // content={`https://www.interac8digital.com/blog-article?title=${title}`}
                    content={`https://6f19-2400-adc1-16b-f000-5922-b9bf-49ff-c559.ngrok-free.app/blog-article?title=${title}`}
                  />

                  {/* <meta
                    property="og:image"
                    content={
                      (posts && posts.uagb_featured_image_src) ? posts.uagb_featured_image_src.full[0] : "Fetching title"
                    }
                  /> */}

                  {/* <meta
                    name="image"
                    property="og:image"
                    content={
                      posts && posts.uagb_featured_image_src
                      ? posts.uagb_featured_image_src.full[0]
                      : "Fetching title"
                  }
        /> */}

                  {/* {posts && posts.uagb_featured_image_src && (
                    <meta
                      name="image"
                      property="og:image"
                      content={posts.uagb_featured_image_src.full[0]}
                    />
                  )} */}

                  {/* <meta
                    name="image"
                    property="og:image"
                    content="https://randomuser.me/api/portraits/med/men/75.jpg"
                  /> */}

                  <meta
                    name="description"
                    property="og:description"
                    content="Digital Marketing Agency"
                  />

                  <meta name="author" content="Interac8Digital" />
                </Helmet>

                {/*  */}
              </>
            ) : (
              <InfinitySpin
                height="175"
                width="175"
                radius="9"
                color="#612d91"
                ariaLabel="loading"
                wrapperStyle
                wrapperClass
              />
            )}
          </div>

          {posts && (
            <div
              className="blog-new-page-main-container-secondary-content"
              dangerouslySetInnerHTML={{ __html: posts.content.rendered }}
            ></div>
          )}

          <div className="share-this-post">
            <p>Share this post</p>

            <div className="share-this-post-social-media">
              <WhatsappShareButton>
                <FaWhatsappSquare size={25} color="#612D91" />
              </WhatsappShareButton>

              <FacebookShareButton url="interac8digital.com">
                <FaFacebook size={25} color="#612D91" />
              </FacebookShareButton>

              <LinkedinShareButton url="interac8digital.com">
                <FaLinkedin size={25} color="#612D91" />
              </LinkedinShareButton>
            </div>
          </div>
        </div>

        <Footer />
      </div>


    </>
  );
}
